.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.centered {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

table {
  border: 1px solid #000;
  border-spacing: 0;
}

td {
  padding: 0.5rem;
}

td,
th {
  border-bottom: 1pt solid black;
}
tr:last-child td {
  border: 0;
}

.failure {
  background: rgba(238, 0, 0, 0.774);
}

.startup_failure {
  background: rgb(255, 0, 0);
}

.success {
  background: rgba(33, 153, 43, 0.37);
}
